* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
  color: #3f3e41;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}

header {
  top: 0;
  right: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 12%;
  z-index: 100;
  position: fixed;
}

.fa-arrow-left {
  display: flex;
  padding-right: 1rem;
}

.navigation {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.sublogo {
  color: #6b2da8;
}

nav {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1.5rem;
}

nav a {
  font-weight: 550;
  border-bottom: 2px solid transparent;
  padding: 0.5rem 0 0.5rem;
  transition: 0.3s ease;
}

nav a:hover {
  color: #6b2da8;
  border-bottom: 2px solid #6b2da8;
}

.user-register {
  display: flex;
  gap: 1rem;
  font-weight: 550;
}

.signout-btn,
.get-help-btn {
  background-color: transparent;
  border-radius: 0.5rem;
  padding: 0.5rem;
  transition: 0.3s ease;
}

.fa-bell {
  transition: 0.3s ease;
}

.fa-bell:hover {
  color: #6b2da8;
}

.signout-btn:hover,
.get-help-btn:hover {
  background-color: #e2dfe3;
}

section {
  height: 100vh;
  width: 100%;
  padding: 5rem 12%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.row1 {
  height: 50%;
  width: 100%;
}
.row2 {
  height: 50%;
  width: 100%;
}

@media (max-width: 480px) {
  .fa-arrow-left {
    display: flex;
    color: #3f3e41;
  }

  header {
    padding: 1rem;
    width: 100%;
    border-bottom: 0.5px solid #afafaf;
    z-index: 100;
    background-color: #ffffff;
  }

  .navigation {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .navigation nav {
    display: none;
  }

  .user-register {
    display: none;
  }
}
